import React from 'react'
import classes from '../Classes/Qualifications.module.css'
import image from '../images/about.jpg'

const Qualifications = () =>{

    return(
        <React.Fragment>
            <section className={classes.skills}>
                <div className={classes.container}>
                    <div className={`${classes.row} ${classes.alignItemCenter}`}>
                        <div className={classes.skillsContent}>
                            <div className={classes.row}>
                                <div className={classes.sectionTitle}>
                                    <h1>PROFESSIONAL SKILLS</h1>
                                    <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Ducimus, facilis ut molestias laudantium ipsa ad.</p>
                                </div>
                            </div>
                            <div className={`${classes.row} ${classes.m}`}>
                                <div className={classes.skillBox}>
                                    <h6>HTML5</h6>
                                    <div className={classes.skillBar}>
                                        <div className={classes.skillBarIn} style={{width:'92%'}}>
                                        <span>92%</span>
                                    </div>
                                    </div>
                                    
                                </div>
                                <div className={classes.skillBox}>
                                    <h6>JAVASCRIPT</h6>
                                    <div className={classes.skillBar}>
                                        <div className={classes.skillBarIn} style={{width:'78%'}}>
                                            <span>78%</span>
                                        </div>
                                
                                    </div>
                                    
                                </div>
                                <div className={classes.skillBox}>
                                    <h6>CSS</h6>
                                    <div className={classes.skillBar}>
                                        <div className={classes.skillBarIn} style={{width:'85%'}}>
                                        <span>85%</span>
                                        </div>
                                    </div>
                                    
                                </div>
                                
                            </div>
                        </div>
                        <div className={classes.img}>
                            <img src={image} alt="Image"/>
                        </div>
                    </div>
                </div>
            </section>

            <section className={classes.table}>
                <div className={classes.container}>
                    <div className={classes.row}>
                        <div className={classes.sectionTitle}>
                            <h1>Qualifications</h1>
                            <p>My studies and Degrees</p>
                        </div>
                    </div>
                    <table className={classes.tableContent}>
                        <thead>
                            <tr>
                                <th>Education</th>
                                <th>Year</th>
                                <th>Institute</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>Metric</td>
                                <td>2016</td>
                                <td>Qazi Group of schools</td>
                            </tr>
                            <tr>
                                <td>Intermediate</td>
                                <td>2018</td>
                                <td>Forman Chiristain College Lahore</td>
                            </tr>
                            <tr>
                                <td>BS-IT(2-years)</td>
                                <td>2020</td>
                                <td>Government College Township Lahore</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </section>
        </React.Fragment>
    )
}
export default Qualifications