import React, {useState} from "react"
import classes from '../Classes/Navbar.module.css'
import {NavLink} from "react-router-dom"
const Navbar = () =>{

    // Display controls
    const [show, setshow] = useState(false)
    const handleShow = () =>{
        setshow(!show)
    }
    return(
        <header className={classes.header}>
            <div className={classes.container}>
                <div className={classes.row}>
                    <div className={classes.brand}>
                        <a href="" className={classes.logo}>noctis</a>
                    </div>
                    <div className={classes.hamBurger}>
                        <i className="fa fa-bars" onClick={handleShow}></i>
                    </div>
                    <div className={`${classes.navbar} ${show? classes.show:null}`}>
                        <ul>
                            <NavLink to="/" exact={true} activeClassName={classes.active}><li><a onClick={handleShow}>Home</a></li></NavLink>
                            <NavLink to="/about" activeClassName={classes.active}><li><a onClick={handleShow}>About</a></li></NavLink>
                            <NavLink to="/services" activeClassName={classes.active}><li><a onClick={handleShow}>Services</a></li></NavLink>
                            <NavLink to="/qualifications" activeClassName={classes.active}><li><a onClick={handleShow}>Qualifications</a></li></NavLink>
                            <NavLink to="/contact" activeClassName={classes.active}><li><a onClick={handleShow}>Contact Us</a></li></NavLink>
                        </ul>
                    </div>
                </div>
            </div>
        </header>
    )
}
export default Navbar