import React from 'react'
import classes from '../Classes/Contact.module.css'
const Contact = () =>{

    return(
        <div className={classes.contact}>
           <div className={classes.container}>
                <div className={classes.row}>
                    <div className={`${classes.sectionTitle} ${classes.textCenter}`}>
                        <h1>Get in Touch</h1>
                    </div>
                </div>
                <div className={classes.row}>
                    <div className={classes.form}>
                        <div className={classes.text}>
                            <h2>Drop us a line</h2>
                            <p>We are here to answer any Question you may have</p>
                        </div>
                        <div className={`${classes.row} ${classes.space_between}`}>
                            <div className={classes.col_6}>
                                <input type="text" className={classes.form_control} placeholder="Name" name="" id=""/>
                            </div>
                            <div className={classes.col_6}>
                                <input type="text" className={classes.form_control} placeholder="Email" name="" id=""/>
                            </div>
                        </div>
                        <div className={classes.row}>
                            <div className={classes.col_12}>
                                <textarea name="" id="" className={classes.form_control} placeholder="Your Comment"></textarea>
                            </div>
                        </div>
                        <div className={classes.row}>
                            <div className={`${classes.btn} ${classes.align_right}`}>
                                <button>Submit</button>
                            </div>
                        </div>
                    </div>
                </div>
           </div>
        </div>
    )
}
export default Contact