import React from 'react'
import classes from '../Classes/Services.module.css'
const Services = () =>{
    return(
        <section className={classes.services}>
        <div className={classes.container}>
            <div className={classes.row}>
                <div className={`${classes.sectionTitle}  ${classes.textCenter}`}>
                    <h1>Our Services</h1>
                </div>
            </div>
            <div className={classes.row}>
                <div className={classes.content}>
                    <div className={classes.row}>
                        <div className={classes.box}>
                            <div className={classes.icon}><i className="fas fa-code"></i></div>
                            <h5>HTML</h5>
                            <p>Lorem, ipsum dolor sit amet consectetur adipisicing elit. Nobis, veritatis.</p>
                        </div>
                        <div className={classes.box}>
                            <div className={classes.icon}><i className="fab fa-css3"></i></div>
                            <h5>CSS</h5>
                            <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Fuga, veritatis!</p>
                        </div>
                        <div className={classes.box}>
                            <div className={classes.icon}><i className="fab fa-js-square"></i></div>
                            <h5>JAVASCRIPT</h5>
                            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Quisquam, quia.</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        </section>
    )
}
export default Services